<template>
	<div class="userGundong">
		<ul>
			<li v-for="item in userList">
				<img :src="item.img" />
				<p>{{item.con}}</p>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name:'scrollTop',
	data () {
		return {
			userList:[{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'1创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			},{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'2创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			},{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'3创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			},{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'4创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			},{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'5创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			},{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'6创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			},{
				'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
				'con':'7创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
			}],
			timeUser:null
		}
	},
	methods:{
		animationTop () {
			var n = 0;
			var _this = this;
			var arr = _this.userList.slice(0,1);
			_this.userList = _this.userList.concat(arr)
			this.timeUser = setInterval(function () {
				n ++;
				$(".userGundong ul").animate({top: -0.42*n + "rem"},800,'linear',function () {
					if(n == _this.userList.length-1){
						n = 0;
						$(".userGundong ul").css("top",'0')
					}
				});
			},2000)
		}
	},
	mounted () {
		this.animationTop();
	},
	beforeDestroy () {
		clearInterval(this.timeUser)
	}
}
</script>

<style scoped="scoped" lang="scss">
.userGundong{
	width: 100%;
	height: 0.42rem;
	background: #FFFFFF;
	overflow: hidden;
	ul{
		position:relative;
		li{
			display: flex;
			align-items: center;
			padding: 0 0.1rem;
			height: 0.42rem;
			img{
				width: 0.25rem;
				height: 0.25rem;
				border-radius: 50%;
			}
			p{
				width: 3.17rem;
				font-size: 0.14rem;
				color: #666666;
				margin-left: 0.1rem;
				overflow: hidden;
				text-overflow:ellipsis;
				white-space: nowrap;
			}
		}
	}
}
</style>


