<template>
	<div class="banner">
		<div class="swiper-container">
		    <div class="swiper-wrapper">
		      <div class="swiper-slide" v-for="(item,index) in bannerData">
		      	<img :src="item" />
		      </div>
		    </div>
		  </div>
	</div>
</template>

<script>
	export default {
		name:'bannerDetail',
		props: {
			bannerData: Array,
		},
		data () {
			return {
				
			}
		},
		mounted() {
			console.log(this.bannerData)
			var swiper = new Swiper('.swiper-container', {
		      spaceBetween: 0,
		      autoplay : 3000,
		      autoplayDisableOnInteraction: false
		    });
		}
	}
</script>

<style scoped="scoped" lang="scss">
    .swiper-container {
      width: 100%;
      height: 3.75rem;
      .swiper-slide{
      	img{
      		width: 100%;
      		height: 100%;
      	}
      }
    }
</style>


