<template>
	<div id="helpCollage">
		<h3>已有1000人参与拼团</h3>
		<div class="userList">
			<ul>
				<li v-for="(item, index) in userList">
					<img :src="item.img" />
					<p>{{item.con}}}</p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name:'partakeCollage',
		data () {
			return {
				userList:[{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'1创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				},{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'2创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				},{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'3创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				},{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'4创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				},{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'5创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				},{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'6创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				},{
					'img':'https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60',
					'con':'7创业真可爱 0元拿了【颈椎按摩器】治疗哈哈哈哈哈ddddd'
				}]
			}
		},
		methods:{
			animationTop () {
				var n = 0;
				var _this = this;
				var arr = _this.userList.slice(0,3);
				_this.userList = _this.userList.concat(arr);
				setInterval(function () {
					n ++;
					$(".userList ul").animate({top: -0.42*n + "rem"},800,'linear',function (){
						if( n == _this.userList.length-3 ){
							n = 0;
							$(".userList ul").css("top",0)
						}
					});
				},2000)
			}
		},
		mounted () {
			this.animationTop()
		}
	}
</script>

<style scoped="scoped" lang="scss">
#helpCollage{
	width: 3.25rem;
	height: 1.66rem;
	background: rgba(255,208,46,0.17);
	border-radius: 0.1rem;
	h3{
		font-size: 0.14rem;
		color: #914E1E;
		line-height: 0.3rem;
		padding-left: 0.065rem;
		border-bottom:0.005rem solid #BFBFBF;
	}
	.userList{
		overflow: hidden;
		height: 1.3rem;
	}
	ul{
		padding:0 0.07rem;
		position: relative;
		li{
			display:flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 0.125rem;
			img{
				width: 0.295rem;
				height: 0.295rem;
				border-radius: 50%;
				margin-right: 0.13rem;
			}
			p{
				font-size: 0.14rem;
				color: #545454;
				overflow:hidden;
			    text-overflow:ellipsis;
			    white-space:nowrap
			}
		}
	}
}
</style>


