<template>
	<div id="container">
		<banner :bannerData = 'bannerList'></banner>
		<div class="detailInfo">
			<div class="detail_one">
				<span><i>1345人</i>已免费拿</span>
				<span>价值69元</span>
			</div>
			<h3>麓屿 玻璃罐杯子甜点 可可抹茶千层盒子蛋糕甜品台下午茶厦门同城</h3>
			<div class="collage_gl">
				<div><span><i></i>拼团免费拿</span><span @click="goPupop">免单攻略</span></div>
				<div>邀请好友，免费得好货</div>
			</div>
		</div>
		<collage-recommend></collage-recommend>
		<div class="detail-bottom">
			<div class="more" @click="goCommodityList">免费拿更多</div>
			<div class="collage" @click="goInvitingFriends">拼团免费拿</div>
		</div>
		<popup :choice = 'choice' @reset="popupReset"></popup>
	</div>
</template>

<script>
	import Banner from './detailAssembly/BannerDetail'
	import CollageRecommend from './assembly/CollageRecommend'
	import Popup from './assembly/Popup'
	export default {
		name:'detail',
		components:{
			Banner,
			CollageRecommend,
			Popup//弹窗
		},
		data () {
			return {
				bannerList:['https://img.alicdn.com/i2/2497896331/TB24x8cFKGSBuNjSspbXXciipXa_!!2497896331.jpg_q60','https://img.alicdn.com/i4/2497896331/TB2FXqjFH1YBuNjSszhXXcUsFXa_!!2497896331.jpg_q6','https://img.alicdn.com/i1/2497896331/TB2uGpfxvuSBuNkHFqDXXXfhVXa_!!2497896331.jpg_q60','https://img.alicdn.com/i3/2497896331/TB2alkiFqSWBuNjSsrbXXa0mVXa_!!2497896331.jpg_q60'],
				choice:'0'
			}
		},
		methods: {
			goPupop () {
				this.choice='4';
			},
			popupReset (data) {
		     	this.choice=data;
		   },
		   //免费拿更多
		   goCommodityList () {
		   	this.$router.push({path:'/'})
		   },
		   //拼团免费拿   去邀请好友页
		   goInvitingFriends () {
		   	this.$router.push({path:'/invitingRegiment'})
		   }
		   
		}
	}
</script>

<style scoped="scoped" lang="scss">
#container{
	background: #FFFFFF;
	padding-bottom: 0.49rem;
}
.detail_one{
	margin-top: 0.09rem;
	margin-left: 0.15rem;
	span{
		&:nth-of-type(1){
			color: #FB3D38;
			font-size: 0.14rem;
			i{
				font-size: 0.18rem;
			}
		}
		&:nth-of-type(2){
			color: #BEBDBD;
			font-size: 0.11rem;
			margin-left: 0.22rem;
		}
	}
}
.detailInfo{
	h3{
		font-size: 0.16rem;
		color: #333333;
		margin-left: 0.16rem;
		line-height: 0.26rem;
		margin-top: 0.19rem;
		margin-bottom: 0.19rem;
		overflow:hidden; 
		text-overflow:ellipsis;
		display:-webkit-box; 
		/*! autoprefixer: off */
		 -webkit-box-orient: vertical;
		/* autoprefixer: on */
		-webkit-line-clamp:2; 
	}
	.collage_gl{
		padding: 0 0.15rem;
		height: 0.7rem;
		background: rgba(255,46,46,0.13);
		div{
			&:nth-of-type(1){
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding-top: 0.08rem;
				span{
					font-size: 0.15rem;
					color: #FF2E2E;
					&:nth-of-type(1){
						i{
							display: inline-block;
							width: 0.21rem;
							height: 0.21rem;
							vertical-align: bottom;
							margin-right: 0.05rem;
							background: url(../assets/img/collage.png) no-repeat;
							background-size:100% 100% ;
						}
					}
					&:nth-of-type(2){
						color: #42210B;
						font-size: 0.12rem;
						text-decoration: underline;
					}
				}
			}
			&:nth-of-type(2){
				font-size: 0.13rem;
				color: #333333;
				margin-top: 0.17rem;
			}
		}
	}
}
.detail-bottom{
	width: 100%;
	height: 0.49rem;
	display: flex;
	position: fixed;
	bottom: 0;
	border-top: 0.01rem solid #CCCCCC;
	div{
		width: 50%;
		height: 100%;
		line-height: 0.49rem;
		font-size: 0.17rem;
		text-align: center;
	}
	.more{
		background: #ffffff;
		color: #333333;
	}
	.collage{
		background: #FF2E2E;
		color: #FFFFFF;
	}
}
</style>


