<template>
	<div class="container">
		<div class="collage" v-if="collageIngFlag">
			<p class="ing">已拼<span>66.66</span>元，还差<span>36</span>元</p>
			<button>邀请好友拼团</button>
			<div>还剩<span>22:37:24</span>过期，快来拼团吧</div>
		</div>
		<div class="collage" v-if="!collageIngFlag && collageSuccessFlag">
			<p class="success">恭喜您，拼团成功</p>
			<button @click.prevent='goReceive'>去领取</button>
			<div>淘宝下单，收货后全额返利，0元获得此商品</div>
		</div>
		<div class="collage" v-if="!collageIngFlag && !collageSuccessFlag">
			<button class="fail">拼团失败</button>
			<div>拼团已过期，去重新拼团或挑选其他商品吧~</div>
		</div>
		<popup :choice = 'choice' @reset="popupReset"></popup>
	</div>
</template>

<script>
	import Popup from './Popup'
	export default {
		name:'goCollage',
		components:{
			Popup//弹窗
		},
		data () {
			return {
				collageSuccessFlag: true,
				collageIngFlag: false,
				choice:'0'
			}
		},
		methods: {
			goReceive () {
				this.choice='1';
			},
			popupReset (data) {
		     	this.choice=data;
		    }
		},
		mounted () {
		}
	}
</script>

<style scoped="scoped" lang="scss">
.collage{
	margin-top: 0.22rem;
	margin-bottom: 0.15rem;
	.ing{
		text-align: center;
		color: #333333;
		font-size: 0.16rem;
		span{
			font-size: 0.16rem;
			color: #FF2E2E;
		}
	}
	.success{
		font-size: 0.16rem;
		color: #FF2E2E;
		text-align: center;
	}
	button{
		width: 2.63rem;
		height: 0.34rem;
		display: block;
		margin: auto;
		margin-top: 0.11rem;
		border-radius: 0.17rem;
		background: #FF2E2E;
		font-size: 0.16rem;
		color: #FFFFFF;
		border: none;
	}
	.fail{
		background: #9C9C9C;
	}
	div{
		font-size: 0.13rem;
		margin-top:0.135rem ;
		text-align: center;
		color: #545454;
	}
}
</style>


