<template>
	<div class="container">
		<div class="collage">
			<p>已拼<span>66.66</span>元，还差<span>36</span>元</p>
			<button @click="goReceive">助力好友拼团</button>
			<div>还剩<span>22:37:24</span>过期，快来拼团吧</div>
		</div>
		<popup :choice = 'choice' @reset="popupReset"></popup>
	</div>
</template>

<script>
	import Popup from './Popup'
	export default {
		name:'goCollage',
		components:{
			Popup//弹窗
		},
		data () {
			return {
				choice:'0'
			}
		},
		methods: {
			goReceive () {
				this.choice='2';//1去领取  2助力好友拼团
			},
			popupReset (data) {
		     	this.choice=data;
		    }
		},
		mounted () {
		}
	}
</script>

<style scoped="scoped" lang="scss">
.collage{
	margin-top: 0.22rem;
	margin-bottom: 0.15rem;
	p{
		text-align: center;
		color: #333333;
		font-size: 0.16rem;
		span{
			font-size: 0.16rem;
			color: #FF2E2E;
		}
	}
	button{
		width: 2.63rem;
		height: 0.34rem;
		display: block;
		margin: auto;
		margin-top: 0.11rem;
		border-radius: 0.17rem;
		background: #FF2E2E;
		font-size: 0.16rem;
		color: #FFFFFF;
		border: none;
	}
	div{
		font-size: 0.13rem;
		margin-top:0.135rem ;
		text-align: center;
		color: #545454;
	}
}
</style>


